















































import {Component, Model, Vue} from 'vue-property-decorator';
import {Button, Field, Tab, Tabs, Uploader} from 'vant';
import Prepare from '@/views/cert/prepare.vue';

import 'vant/lib/tab/style';
import 'vant/lib/tabs/style';
import 'vant/lib/field/style';
import 'vant/lib/button/style';

import PDFJS from 'pdfjs-dist';
import {Cert, CertGia, CertOwn} from '@/entry/diamond';
import {notes, NoteType} from '@/note';

Vue.use(Tab).use(Tabs).use(Field).use(Button).use(Uploader);
@Component({
    components: {Prepare, Tab, Tabs, Field, Button},
})
export default class InputCert extends Vue {

    @Model('input', {default: '1299065112'})
    private giaNumber!: string;
    private giaScan: string = '';
    private active = 0;

    private certOwn = new CertOwn();


    private keyId = 'id';
    private valueId = '';
    private key2 = '';
    private value2 = '';
    private key3 = '';
    private value3 = '';
    private key4 = '';
    private value4 = '';


    public created(): void {
      this.$store.state.title = this.$t('cert.input_cert');
    // todo test
    }

    public onclickGiaNext(): void {
    // todo
      if (this.giaNumber != null) {
          this.$router.push({name: 'cert_gia_frame', params: {giaNumber: this.giaNumber}});
          return;
      }
    }
    public valideKey(key: string): boolean {
        // check parameter
        if (this.$refs[key]) {
            const data = [{key: this.keyId, value: this.valueId},
                {key: this.key2, value: this.value2},
                {key: this.key3, value: this.value3},
                {key: this.key4, value: this.value4}];

            const m = new Map<String, String>();
            let count = 0;

            const v = (this.$refs[key] as any).value as string;
            for (const it of data) {
                if (it.key === v) {
                    count = count + 1;
                }
            }
            return (count > 1);
        } else {
            return false;
        }
    }
    public onclickOtherNext(): void {

        const cert = new CertOwn();
        {
            const data = [{key: this.keyId, value: this.valueId},
                {key: this.key2, value: this.value2},
                {key: this.key3, value: this.value3},
                {key: this.key4, value: this.value4}];
            {
                const m = new Map<String, String>();
                for (const it of data) {
                    m.set(it.key, it.value);
                }

                if (m.size < data.length) {
                    notes.add({type: NoteType.Error, message: this.$t('notifyMsg.contRepeat').toString()});
                    return;
                }
            }

            const c = new Cert();
            c.map.push({key: this.keyId, value: this.valueId});
            if (this.key2) {
                c.map.push({key: this.key2, value: this.value2});
            }
            if (this.key3) {
                c.map.push({key: this.key3, value: this.value3});
            }
            if (this.key3) {
                c.map.push({key: this.key3, value: this.value4});
            }
            cert.cert = c;
        }
        this.certOwn = cert;

        this.$store.state.data.certOwn = this.certOwn;
        this.$router.push({name: 'cert_prepare', params: {}}); // todo certOwn: certOwn.toCert()
    }
    public onFileUpload(event: Event): void {
        const f = event.target as HTMLInputElement;
        if (f && f.files && f.files.length > 0) {
            const reader = new FileReader();
            reader.onload = (file) => {
                const u = reader.result as string;
                if (u) {
                    this.readPdf(u).then((s) => {
                        const reg = new RegExp(/GIA Report Number[ .]+(\w+)Shape and Cutting Style.+Carat Weight[ .]+ ([0-9.]+)\s*caratColor Grade[ .]+(\w+)Clarity Grade[ .]+(\w+)Cut Grade[ .]+(\w+)Polish/).exec(s);
                        if (reg && reg.length === 6) {
                            const cert = new CertOwn();
                            {
                                const c = new CertGia();
                                c.gia = reg[1];
                                c.carat = reg[2];
                                c.color = reg[3];
                                c.clarity = reg[4];
                                c.cut = reg[5];
                                cert.cert = c.toCert();
                            }
                            this.certOwn = cert;

                            this.$store.state.data.certOwn = this.certOwn;
                            this.$router.push({name: 'cert_prepare', params: {}}); // todo certOwn: certOwn.toCert()
                        }
                    }).catch((e: any) => {
                        notes.add({type: NoteType.Error, message: e.toString()});
                        f.value = '';
                    });
                }

            };
            reader.readAsDataURL(f.files[0]);
        }
    }

    public async readPdf(url: string)  {
        const pdf = await PDFJS.getDocument(url).promise;
        const maxPages = pdf.numPages;
        const texts = []; // collecting all page promises
        for (let j = 1; j <= maxPages; j++) {
            const page = await pdf.getPage(j);
            const textContent = await page.getTextContent();
            const txt = textContent.items.map((s) => {
                return s.str;
            }).join('');
            texts.push(txt);
        }

        return texts.join('');
    }

}
